<script>
import { mapState } from "vuex";
import { getShopAuthInfoByShopId } from "@/api/shop/shopAttestation";
export default {
  data() {
    return {
      noAttestation: false,
      authType: 0, // 1.个人认证  2.企业认证
      ruleForm: {}
    };
  },
  created() {
    console.log("shop", this.shop);
    this.getData();
  },
  methods: {
    // 获取数据
    getData() {
      getShopAuthInfoByShopId(this.shop.shopId).then(res => {
        console.log("店铺资质数据", res);
        if (res.data == null) this.noAttestation = true;
        if (res.data) {
          this.authType = res.data.authType;
          this.ruleForm = res.data;
        }
      });
    },
    // 个人店铺 - 点击编辑
    submitIndividual() {
      console.log("企业 - 点击了编辑");
      this.$router.push({
        path: "/shop/shopAttestation/individualAttestation",
        query: {
          edit: true
        }
      });
    },
    // 企业 - 点击编辑
    submitBusiness() {
      console.log("企业 - 点击了编辑");
      this.$router.push({
        path: "/shop/shopAttestation/businessAttestation",
        query: {
          edit: true
        }
      });
    },
    // 个人 - 点击编辑
    submitPersonal() {
      console.log("个人 - 点击了编辑");
      this.$router.push({
        path: "/shop/shopAttestation/personalAttestation",
        query: {
          edit: true
        }
      });
    }
  },
  computed: {
    ...mapState(["shop"])
  }
};
</script>
